// 
export const adoptionCurveData = Array.from({ length: 100 }, (_, i) => {
  const x = i
  // 
  const y = Math.exp(-Math.pow((x - 50) / 20, 2) / 2) * 100

  let layer = ''
  let color = '#6B7280'
  if (x <= 2.5) {
    layer = ''
    color = '#3B82F6'
  } else if (x <= 16) {
    layer = ''
    color = '#10B981'
  } else if (x <= 50) {
    layer = ''
    color = '#F59E0B'
  } else if (x <= 84) {
    layer = ''
    color = '#EF4444'
  } else {
    layer = ''
    color = '#6B7280'
  }

  return { x, y, layer, color }
})

